<template>
  <div class="main">
    <div id="app">
      <el-card class="box-card">
        <div class="body-data">
          <el-form :model="bodyData">
            <el-form-item>
              <label style="font-size: 16px;">TDEE:</label>
              <el-input v-model="bodyData.tdee" placeholder="TDEE" type="number"></el-input>
            </el-form-item>
            <el-form-item>
              <label style="font-size: 16px;">今日所需的蛋白質量:</label>
              <el-input v-model="bodyData.protein" placeholder="需要的蛋白質量" type="number"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <!-- 早、中、晚新增按鈕 -->
        <div class="add-meal">
          <el-row>
            <el-button type="primary" @click="addFood(1)" class="meal-button">+ 早餐</el-button>
            <el-button type="primary" @click="addFood(2)" class="meal-button">+ 午餐</el-button>
            <el-button type="primary" @click="addFood(3)" class="meal-button">+ 晚餐</el-button>
            <!-- <el-button type="primary" @click="calculation" class="meal-button">結算</el-button> -->
          </el-row>
        </div>
        <!-- 新增每餐食物内容彈出視窗 -->
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" :before-close="handleClose" :show-close=false
          :close-on-click-modal=false :close-on-press-escape=false>
          <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="10px" class="demo-dynamic"
            :hide-required-asterisk=true>
            <el-form-item v-for="(domain, index) in  dynamicValidateForm[mealType] " :key="domain.key"
              :prop="`${mealType}[${index}].food`" :rules="{ required: true, message: '請輸入食物', trigger: 'blur' }">
              <div class="food-item">
                <label>食物：</label>
                <el-input v-model="domain.food"></el-input>
                <label>熱量：</label>
                <el-input v-model="domain.cal" type="number"></el-input>
                <label>蛋白質：</label>
                <el-input v-model="domain.protein" type="number"></el-input>
                <el-button @click.prevent="removeDomain(domain)" type="danger">删除</el-button>
              </div>
            </el-form-item>
            <el-form-item style="text-align: right; display: flex; justify-content: space-between;">
              <el-button type="primary" @click="addDomain">新增食物</el-button>
              <el-button type="success" @click="submitForm('dynamicValidateForm')">確認</el-button>
              <!-- <el-button @click="resetForm('dynamicValidateForm')">重置</el-button> -->
            </el-form-item>
          </el-form>
        </el-dialog>
        <!-- 畫面顯示的食物清單 -->
        <div class="table">
          <!-- 早 -->
          <div class="breakfast">
            <h3>早餐：</h3>
            <div v-for="(domain, index) in dynamicValidateForm.breakfast" :key="domain.key">
              <div class="meal">
                <div class="food-info">
                  <span>{{ index + 1 + '.' }}{{ domain.food }}</span>
                </div>
                <div class="meal-detail">
                  <span>熱量：{{ domain.cal ? domain.cal + ' kcal' : '未填寫' }}</span>
                  <span>蛋白質：{{ domain.protein ? domain.protein + ' g' : '未填寫' }}</span>
                </div>
              </div>
            </div>
            <div class="meal-detail-total" v-show="totalCal.breakfast != '' || totalProtein.breakfast != ''">
              <span>總共：</span>
              <span> 熱量{{ totalCal.breakfast ? totalCal.breakfast : '0' }} kcal </span>
              <span>/</span>
              <span>蛋白質{{ totalProtein.breakfast ? totalProtein.breakfast : '0' }} g </span>
            </div>
            <hr>
          </div>
          <!-- 中 -->
          <div class="lunch">
            <h3>午餐：</h3>
            <div v-for="(domain, index) in dynamicValidateForm.lunch" :key="domain.key">
              <div class="meal">
                <div class="food-info">
                  <span>{{ index + 1 + '.' }}{{ domain.food }}</span>
                </div>
                <div class="meal-detail">
                  <span>熱量：{{ domain.cal ? domain.cal + ' kcal' : '未填寫' }}</span>
                  <span>蛋白質：{{ domain.protein ? domain.protein + ' g' : '未填寫' }}</span>
                </div>
              </div>
            </div>
            <div class="meal-detail-total" v-show="totalCal.lunch != '' || totalProtein.lunch != ''">
              <span>總共：</span>
              <span> 熱量{{ totalCal.lunch ? totalCal.lunch : '0' }} kcal </span>
              <span>/</span>
              <span> 蛋白質{{ totalProtein.lunch ? totalProtein.lunch : '0' }} g</span>
            </div>
            <hr>
          </div>
          <!-- 晚 -->
          <div class="dinner">
            <h3>晚餐：</h3>
            <div v-for="(domain, index)  in dynamicValidateForm.dinner" :key="domain.key">
              <div class="meal">
                <div class="food-info">
                  <span>{{ index + 1 + '.' }}{{ domain.food }}</span>
                </div>
                <div class="meal-detail">
                  <span>熱量：{{ domain.cal ? domain.cal + ' kcal' : '未填寫' }}</span>
                  <span>蛋白質：{{ domain.protein ? domain.protein + ' g' : '未填寫' }}</span>
                </div>
              </div>
            </div>
            <div class="meal-detail-total" v-show="totalCal.dinner != '' || totalProtein.dinner != ''">
              <span>總共：</span>
              <span>
                熱量{{ totalCal.dinner ? totalCal.dinner : '0' }} kcal
              </span>
              <span>/</span>
              <span>
                蛋白質{{ totalProtein.dinner ? totalProtein.dinner : '0' }} g
              </span>
            </div>
            <hr>
          </div>
          <!-- 總結算 -->
          <div v-show="bodyData.tdee != '' || bodyData.protein != ''">
            <h3>結算：</h3>
            <div class="todaySum">
              <div class="l-item">
                <span style="color: black;">
                  {{ "今日攝取總熱量：" + todayCalSum +" kcal"}}
                </span>
                <br>
                <span style="color: black;">
                  {{ "今日攝取總蛋白：" + todayProteinSum +" g"}}
                </span>
              </div>
              <div class="r-item">
                <span :style="{ color: bodyData.tdee - todayCalSum >= 0 ? 'green' : 'red' }">
                  {{ bodyData.tdee - todayCalSum >= 0 ? "熱量赤字：" : "熱量盈餘：" }}
                  {{ Math.abs(bodyData.tdee - todayCalSum) }} kcal
                </span>
                <br>
                <span :style="{ color: bodyData.protein - todayProteinSum >= 0 ? 'red' : 'green' }">
                  {{ bodyData.protein - todayProteinSum >= 0 ? "蛋白質不足：" : "蛋白質盈餘：" }}
                  {{ Math.abs(bodyData.protein - todayProteinSum) }} g
                </span>
              </div>
            </div>
          </div>
          <div class="reset">
            <!-- openAI需要key才能用 -->
            <!-- <div style="display: flex; margin-top: 5px;">
              <el-dialog :visible.sync="foodDialogVisible" title="查詢結果">
                <span>{{ foodMessage }}</span>
              </el-dialog>
              <el-input v-model="foodNmae" placeholder="搜尋食物作爲參考！ ex:鷄蛋一顆"></el-input>
              <el-button @click="gptApi"
                style="background-color:lightblue; margin-left: 5px; font-weight: 500; border:1px ridge;">搜尋食物</el-button>
            </div> -->
            <el-button @click="clearAll" type="danger" style="width: 100%; margin-top: 20px;">
              清除全部
            </el-button>
            <div style="font-size: 12px;margin-top: 10px;">
              *雖然影響增肌跟減脂的因素很多，但是熱量及蛋白質相對好計算，而本網站的功能是讓您可以快速的計算當天總攝取。
            </div>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>


export default {
  data() {
    return {
      foodDialogVisible: false,
      dialogVisible: false,
      mealType: "",
      //三餐結算
      todayCalSum: "",
      todayProteinSum: "",
      // openAI API
      // openaiApiKey: "",
      // foodNmae: "",
      // foodMessage: "",
      bodyData:
      {
        tdee: "",
        protein: ""
      },
      totalCal: {
        breakfast: "",
        lunch: "",
        dinner: ""
      },
      totalProtein: {
        breakfast: "",
        lunch: "",
        dinner: ""
      },
      //彈出的Dialog表單
      dynamicValidateForm: {
        breakfast: [],
        lunch: [],
        dinner: []
      },
    }
  },
  mounted() {
    this.getItems()
    this.totalToday()
    // this.loadApiKeys()
  },
  watch: {
    //輸入新的值直接自動加到LocalStorage
    bodyData: {
      handler(newData, oldData) {
        localStorage.setItem('bodyData_Tdee', newData.tdee)
        localStorage.setItem('bodyData_Protein', newData.protein)
      }, deep: true
    }
  }
  ,
  methods: {
    //需要openAI的key才能用
    // loadApiKeys() {
    //   axios.get('./api_keys.json')
    //     .then(response => {
    //       this.openaiApiKey = response.data.openai;
    //       // console.log(this.openaiApiKey)
    //     })
    //     .catch(error => {
    //       console.error('Error loading API keys:', error);
    //     });
    // },
    // gptApi() {
    //   axios({
    //     method: 'post',
    //     url: 'https://api.openai.com//v1/chat/completions',
    //     headers: {
    //       'Authorization': 'Bearer key...,
    //       'Content-Type': 'application/json'
    //     },
    //     data: {
    //       model: "gpt-3.5-turbo",
    //       messages: [{ "role": "user", "content": `我會輸入食物我要你找食物的蛋白質及熱量，只回復我該食物蛋白質及熱量多少作爲參考，或是回答無法得知該食物訊息，我要找的食物為${this.foodNmae}` }],
    //       temperature: 0,
    //     }
    //   }).then(({ data }) => {
    //     const { message } = data.choices[0]
    //     this.foodMessage = message.content
    //     this.foodDialogVisible = true
    //   }).catch(error => {
    //     console.error('Error:', error);
    //   });
    // },

    //點擊新增食物後，畫面會移到最下面避免擋到
    handleScrollToBottom() {
      const dialogForm = document.querySelector('.el-dialog__wrapper');
      if (dialogForm) {
        dialogForm.scrollTop = dialogForm.scrollHeight;
      }

    },
    addFood(mealType) {
      this.dialogVisible = true
      if (mealType === 1) {
        this.mealType = "breakfast"
      } else if (mealType === 2) {
        this.mealType = "lunch"
      } else if (mealType === 3) {
        this.mealType = "dinner"
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.calculation()
          this.totalToday()
          this.removeEmptyDomain()
          this.persist()
          this.dialogVisible = false
        } else {
          this.removeEmptyDomain()
          this.dialogVisible = false
          // return false;
        }
      });
    },

    removeDomain(item) {
      var index = this.dynamicValidateForm[this.mealType].indexOf(item)
      if (index !== -1) {
        this.dynamicValidateForm[this.mealType].splice(index, 1)
      }
    },
    //判斷在哪一餐中加入
    addDomain() {
      this.dynamicValidateForm[this.mealType].push({
        food: '',
        cal: '',
        protein: '',
        key: Date.now()
      });
      setTimeout(() => {
        this.handleScrollToBottom();
      }, 0);
    },
    //如果新增時沒有輸入食物名稱，移除那個空的新增欄位
    removeEmptyDomain() {
      const foods = this.dynamicValidateForm[this.mealType]
      for (let i = foods.length - 1; i >= 0; i--) {
        if (!foods[i].food.trim()) {
          foods.splice(i, 1)
        }
      }
    },
    handleClose() {
      this.dialogVisible = false
    }
    , calculation() {
      //計算每日熱量
      this.totalCal.breakfast = this.dynamicValidateForm.breakfast.reduce((accumulator, currentValue) => {
        return accumulator + (parseFloat(currentValue.cal) || 0)
      }, 0)
      this.totalCal.lunch = this.dynamicValidateForm.lunch.reduce((accumulator, currentValue) => {
        return accumulator + (parseFloat(currentValue.cal) || 0)
      }, 0)
      this.totalCal.dinner = this.dynamicValidateForm.dinner.reduce((accumulator, currentValue) => {
        return accumulator + (parseFloat(currentValue.cal) || 0)
      }, 0)
      //計算每日蛋白質
      this.totalProtein.breakfast = this.dynamicValidateForm.breakfast.reduce((accumulator, currentValue) => {
        return accumulator + (parseFloat(currentValue.protein) || 0)
      }, 0)
      this.totalProtein.lunch = this.dynamicValidateForm.lunch.reduce((accumulator, currentValue) => {
        return accumulator + (parseFloat(currentValue.protein) || 0)
      }, 0)
      this.totalProtein.dinner = this.dynamicValidateForm.dinner.reduce((accumulator, currentValue) => {
        return accumulator + (parseFloat(currentValue.protein) || 0)
      }, 0)
    }
    //結算加總
    , totalToday() {
      this.todayCalSum = Object.values(this.totalCal).map(item => parseFloat(item) || 0).reduce((x, y) => x + y, 0)
      this.todayProteinSum = Object.values(this.totalProtein).map(item => parseFloat(item) || 0).reduce((x, y) => x + y, 0)
      // console.log(this.todayProteinSum, "todayProteinSum")
    }
    ,
    clearAll() {
      this.$confirm('確定清除所有資料？', {
        confirmButtonText: '確定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          // message: '已清除全部資料!'
        });
        localStorage.clear();
        location.reload();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    //輸入的資料存入localStorage
    persist() {
      //輸入的TDEE及蛋白質所需
      localStorage.setItem('bodyData_Tdee', JSON.stringify(this.bodyData.tdee))
      localStorage.setItem('bodyData_Protein', JSON.stringify(this.bodyData.protein))
      //三餐
      const meals = ['breakfast', 'lunch', 'dinner']
      meals.forEach(meal => {
        //三餐的數據
        localStorage.setItem(meal, JSON.stringify(this.dynamicValidateForm[meal]))
        //結算的數據
        localStorage.setItem(`totalCal_${meal}`, JSON.stringify(this.totalCal[meal]))
        localStorage.setItem(`totalProtein_${meal}`, JSON.stringify(this.totalProtein[meal]))
      })
    },
    //獲取LocalStorage資料放入相對應model
    getItems() {
      if (localStorage.getItem('bodyData_Tdee')) {
        this.bodyData.tdee = JSON.parse(localStorage.getItem('bodyData_Tdee'))
      }
      if (localStorage.getItem('bodyData_Protein')) {
        this.bodyData.protein = JSON.parse(localStorage.getItem('bodyData_Protein'))
      }
      const meals = ['breakfast', 'lunch', 'dinner']
      meals.forEach(meal => {
        if (localStorage.getItem(meal)) {
          this.dynamicValidateForm[meal] = JSON.parse(localStorage.getItem(meal))
        }

        if (localStorage.getItem(`totalCal_${meal}`)) {
          this.totalCal[meal] = JSON.parse(localStorage.getItem(`totalCal_${meal}`))
        }

        if (localStorage.getItem(`totalProtein_${meal}`)) {
          this.totalProtein[meal] = JSON.parse(localStorage.getItem(`totalProtein_${meal}`))
        }
      })
    },
  },
  computed: {
    dialogTitle() {
      switch (this.mealType) {
        case "breakfast":
          return "早餐";
        case "lunch":
          return "午餐";
        case "dinner":
          return "晚餐";
      }
    }
  }
}
</script>

<style>
