  display: flex;
  justify-content: center;
  align-items: center;

  .box-card {
    min-height: 100vh;
    width: 30vw;

    .body-data {
      .el-form-item {
        margin-bottom: 10px;
        ;
      }
    }

    .add-meal {
      display: flex;
      justify-content: space-evenly;

      .meal-button {
        margin: 0 10px;
      }


    }

    .food-item {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .el-input {
        margin-right: 10px;
      }

      label {
        white-space: nowrap;
      }
    }

    .meal {
      display: flex;
      justify-content: space-between;
    }

    .food-info {
      span {
        font-size: 14px;
      }
    }

    .meal-detail {
      span {
        margin-right: 10px;
        font-size: 14px;
      }
    }

    .meal-detail-total {
      text-align: end;
      margin-top: 5px;
      font-weight: 600;
      color: green;

    }

    .todaySum {
      display: flex;
      justify-content: space-between;

      span {
        color: red;
      }
    }
  }

  @media screen and (orientation: portrait) and (max-width:1024px) {
    .box-card {
      width: 100vw;

      .food-item {
        display: block;

        .el-button {
          float: right;
          margin-top: 10px;
        }

      }

      .el-form-item__error {
        top: 85%
      }

      .el-dialog {
        width: 100vw;
      }
    }


  }

  @media screen and (max-device-width: 1400px) and (orientation: landscape) {
